import React from "react";
import { withRouter } from "react-router-dom";
import {
  Container,
  Card,
  CardContent,
  Typography,
  withStyles,
  Grid,
  Button,
} from "@material-ui/core";
import { EdittablesContext } from "../../../contexts/index";

import DateFnsUtils from "@date-io/date-fns";
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from "@material-ui/pickers";
import MaterialTable from "material-table";
import axios from "axios";
import config from "../../../config";
import moment from "moment";

// style
const styles = (theme) => ({
  root: {
    width: "100%",
    background: theme.palette.mainbackground.default,
    opacity: 1,
    padding: "30px 0px",
    overflowX: "hidden",
  },
  signInButtonerr: {
    margin: "20px 0px",
    padding: "6px 8px",
    color: theme.palette.secondary.main,
    textTransform: "none",
    fontWeight: 600,
    boxShadow: "0px 10px 15px #70707028",
    fontSize: "13px",
    background: "#FF000012 0% 0% no-repeat padding-box",
    border: " 1px solid #FF0000",
    borderRadius: "8px",
    opacity: 1,
    float: "left",
    "&:hover": {
      background: "#FF000012 0% 0% no-repeat padding-box",
    },
  },
  title: {
    fontSize: 16,
    fontWeight: 600,
    [theme.breakpoints.down("xs")]: {
      fontSize: 14,
    },
    // color: "#012834"
  },
  card: {
    background: theme.palette.background.paper,
    boxShadow: "0px 5px 10px #00000014",
    borderRadius: 16,
    opacity: 1,
    margin: "20px 0px",
    padding: 24,
    "& .MuiPaper-elevation2": {
      boxShadow: "none",
      // border: "1px solid #c9caca30"
    },
    "& .MuiInput-root": {
      // border: "1px solid #c9caca59",
      borderRadius: 6,
      padding: 4,
    },
    "& .MuiInput-underline:before,.MuiInput-underline:hover:not(.Mui-disabled):before,.MuiInput-underline:after": {
      borderBottom: "unset",
    },
    "& .MuiToolbar-regular": {
      minHeight: 0,
    },
    "& .MTableToolbar-root-50": {
      marginBottom: 6,
      display: "none",
    },
  },
  summary: {
    cursor: "pointer",
    "& .MuiCardContent-root:last-child": {
      padding: 0,
    },
  },
  card_title: {
    borderTopLeftRadius: 8,
    borderTopRightRadius: 8,
    // borderTop: `2px solid ${theme.palette.mainbackground.default}`,
    // borderRight: `2px solid ${theme.palette.mainbackground.default}`,
    // borderLeft: `2px solid ${theme.palette.mainbackground.default}`,

    borderBottom: `2px solid ${theme.palette.mainbackground.default}`,
    fontWeight: "bold",
    fontSize: 18,
    padding: "0px 0px 20px 0px",
    // [theme.breakpoints.only('xs')]: {
    //   height: '20px'
    // }
  },
  text_title: {
    padding: "6px 0px",
    opacity: 0.9,
  },
  body: {
    padding: "30px 0px 0px",
  },
  btns: {
    margin: "20px 0px",
  },
  clear: {
    boxShadow: "0px 3px 6px #0049903D",
    border: `1px solid ${theme.palette.primary.main}`,
    opacity: 0.9,
    fontSize: 12,
    borderRadius: 8,
    fontWeight: 600,
    marginRight: 12,
  },
  submit: {
    border: `2px solid ${theme.palette.primary.main}`,
    background: theme.palette.primary.main,
    boxShadow: "0px 3px 6px #0049903D",
    borderRadius: 8,
    margin: "0px 10px 0px 0px",
    fontSize: 12,
    opacity: 0.9,
    "&:hover": {
      background: theme.palette.primary.main,
    },
  },
  d_icon: {
    margin: "auto",
    width: "100%",
    cursor: "pointer",
    textAlign: "center",
    color: theme.palette.primary.main,
    fontSize: 50,
  },
  d_title: {
    fontSize: 16,
    fontWeight: 600,
    textAlign: "center",
    color: theme.palette.secondary.main,
    opacity: 0.8,
    letterSpacing: 1,
    paddingBottom: "12px",
  },
  d_btn: {
    textAlign: "center",
    margin: "auto",
    display: "flex",
    height: "40px",
    color: theme.palette.secondary.contrastText,
    fontSize: 16,
    textTransform: "capitalize",
    fontWeight: 600,
    boxShadow: "0px 10px 15px #70707028",
    background: theme.palette.primary.main,
    transition: "0.5s",
    "& button": {
      padding: 8,
    },
    "&:hover": {
      transition: "0.5s",
      background: theme.palette.primary.main,
      boxShadow: "0px 10px 15px #70707028",
      opacity: 0.9,
    },
  },
  table: {
    fontSize: "12px",
  },
});
// end
class TransactionFilter extends React.Component {
  // state
  constructor(props) {
    super(props);
    this.state = {
      institution: [],
      institutionvalue: "",
      data: [],
      status: { value: "All", label: "0" },
      fromdate: null,
      todate: null,
      filtererr: false,

      menuvalue: "",
      submenu: [],
      submenuvalue: "",
    };
  }
  // end
  Clear = () => {
    this.setState({
      fromdate: null,
      todate: null,
    });
  };
  // input handle change funcy
  handleChange = (n, v) => {
    debugger;
    const state = this.state;
    this.setState({
      ...state,
      [n]: v,
    });
  };

  handleTransaction = async (val) => {
    this.context.setData({ ...this.context, transactiontable: val });
    this.props.history.push("/cardholder/Transaction_filter_details");
  };
  // handle edit funcy

  apiFetchEdit = async (api, playod) => {
    await axios
      .post(`${config.api_url}${api}`, playod)
      .then((res) => {
        // this.props.history.push(url);
      })
      .catch((error) => {
        this.setState({
          errdata: error.message,
        });
      });
  };

  componentDidMount = async () => {
    const val = this.context.transactiondetails;
    const state = this.state;
    const body = {
      accNo: val?.accountDetailsResp[0].accNo,
      clearPan: val?.accountDetailsResp[0].cardDetailsResp[0].clearPan,
      endDate: state?.todate ? state.todate : "",
      endRecordNo: 0,
      instCode: "MYNT",
      pflag: "S",
      reqId: "",
      reqMsgId: "",
      rrNo: "",
      startDate: state?.fromdate ? state.fromdate : "",
      startRecordNo: 0,
      vpan: val?.accountDetailsResp[0].cardDetailsResp[0].vpan,
    };
    await this.apiFetchEdit(
      "api/v1/gettransactiondetails",
      body,
      "/Transaction_filter_details"
    );
  };

  handlesubmit = async () => {
    const val = this.context.transactiondetails;
    const state = this.state;
    const body = {
      accNo: val.accountDetailsResp[0].accNo,
      clearPan: val.accountDetailsResp[0].cardDetailsResp[0].clearPan,
      endDate: state.todate ? state.todate : "",
      endRecordNo: 0,
      instCode: "MYNT",
      pflag: "S",
      reqId: "",
      reqMsgId: "",
      rrNo: "",
      startDate: state.fromdate ? state.fromdate : "",
      startRecordNo: 0,
      vpan: val.accountDetailsResp[0].cardDetailsResp[0].vpan,
    };
    await this.apiFetchEdit(
      "api/v1/gettransactiondetails",
      body,
      "/Transaction_filter_details"
    );
  };

  dateval = (e) => {
    var keycode = e.which ? e.which : e.keyCode;
    if (keycode <= 48 && keycode >= 57) {
      return true;
    } else {
      e.preventDefault();
      return false;
    }
  };
  render() {
    const { classes } = this.props;

    const {
      data,

      todate,
      fromdate,

      filtererr,
    } = this.state;

    return (
      <div className={classes.root}>
        <Container>
          {/* Breadcrumbs Component */}

          {/* end */}
          {/* Card Component in add form in modify user*/}
          <Card className={classes.card} variant="outlined">
            <CardContent className={classes.card_title}>Filter By</CardContent>
            <div className={classes.body}>
              <Grid container spacing={6}>
                <Grid item xs={12} sm={6}>
                  <MuiPickersUtilsProvider utils={DateFnsUtils}>
                    <Typography
                      variant="subtitle2"
                      className={classes.text_title}
                    >
                      From Date
                    </Typography>
                    <KeyboardDatePicker
                      style={{ width: "100%", margin: 0 }}
                      inputVariant="outlined"
                      margin="normal"
                      format="yyyy-MM-dd"
                      value={this.state.fromdate}
                      onChange={(e) => this.handleChange("fromdate", e)}
                      minDate={`${
                        new Date().getMonth() + 1
                      }/01/${new Date().getFullYear()}`}
                      onKeyPress={(e) => this.dateval(e)}
                      placeholder=""
                      KeyboardButtonProps={{
                        "aria-label": "change date",
                      }}
                      size="small"
                    />
                  </MuiPickersUtilsProvider>
                </Grid>
                <Grid item xs={12} sm={6}>
                  <MuiPickersUtilsProvider utils={DateFnsUtils}>
                    <Typography
                      variant="subtitle2"
                      className={classes.text_title}
                    >
                      To Date
                    </Typography>
                    <KeyboardDatePicker
                      style={{ width: "100%", margin: 0 }}
                      inputVariant="outlined"
                      margin="normal"
                      format="yyyy-MM-dd"
                      minDate={moment(fromdate)}
                      value={this.state.todate}
                      onKeyPress={(e) => this.dateval(e)}
                      onChange={(e) => {
                        this.handleChange("todate", e);
                      }}
                      placeholder=""
                      KeyboardButtonProps={{
                        "aria-label": "change date",
                      }}
                      size="small"
                    />
                  </MuiPickersUtilsProvider>
                </Grid>
              </Grid>

              <Grid
                container
                direction="row"
                justify="space-between"
                alignItems="center"
                className={classes.btns}
              >
                <Grid item>
                  {filtererr && (
                    <Button className={classes.signInButtonerr}>
                      {!this.state.fromdate && !this.state.todate
                        ? "Please select/fill atleast one field"
                        : "No records found"}
                    </Button>
                  )}
                </Grid>
                <Grid item>
                  <Button
                    variant="contained"
                    className={classes.submit}
                    onClick={() => this.handlesubmit()}
                  >
                    Search
                  </Button>
                  <Button
                    className={classes.clear}
                    onClick={() => this.Clear()}
                  >
                    Clear
                  </Button>
                </Grid>
              </Grid>
            </div>
            {/* table Component */}
            <br />
            <br /> <br />
            <CardContent className={classes.card_title}>
              Transaction Details
            </CardContent>
            <div style={{ background: "#F4F5F8", padding: 2, borderRadius: 4 }}>
              {" "}
              <MaterialTable
                className={classes.table}
                title={false}
                options={{
                  search: false,
                  sorting: true,
                  pageSize: 5,

                  pageSizeOptions: [5, 10, 50, 100, 200],
                  actionsColumnIndex: -1,
                  rowStyle: (rowData) => ({
                    backgroundColor:
                      rowData.tableData.id % 2 === 0 && "#F8F8FA",
                  }),
                }}
                columns={[
                  {
                    field: "TranscType",
                    title: "Transaction Type",
                  },
                  {
                    field: "TranscID",
                    title: "Transaction ID",
                  },
                  {
                    field: "TranscStatus",
                    title: "Transaction Status",
                  },
                  {
                    field: "TermCode",
                    title: "Terminal Code",
                  },
                  {
                    field: "BillAmount",
                    title: "Billing Amount",
                  },
                  {
                    field: "Debit/Credit",
                    title: "Debit/ Credit",
                  },
                  {
                    field: "TransactionDate",
                    title: "Transaction Date",
                  },
                ]}
                data={data}
                actions={[
                  {
                    icon: "save",
                    tooltip: "View",
                    onClick: (event, rowData) => {
                      this.handleTransaction(rowData);
                    },
                  },
                ]}
              />
            </div>
            {/* end */}
          </Card>
          {/* end */}
        </Container>
      </div>
    );
  }
}
TransactionFilter.contextType = EdittablesContext;
export default withStyles(styles)(withRouter(TransactionFilter));
